.parent {
  display: flex;
  /* 
    nowrap: 默认值，显示在同一行，不换行
    wrap: 一行显示不了时换行显示
    wrap-reverse: 同 wrap，但是从下向上显示
 */
  flex-wrap: wrap;
}

.son {
  width: calc(calc(100% / 3) - 10px);
  margin: 5px;
  height: 100px;
  background-color: #fda085;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
